import { Card, Col, Row } from "antd";
import { FaLandmark } from "react-icons/fa";
import React from "react";

const CBoxDepartement = ({ data = 0, onClick }) => {
  return (
    <Col span={6}>
      <div onClick={onClick}>
        <Card
          style={{
            backgroundColor: "#078825",
            borderRadius: "10px",
            height: 150,
            width: "100%",
          }}
          hoverable={true}
        >
          <Row>
            <Col span={8}>
              <FaLandmark style={{ fontSize: "60px", color: "white" }} />
            </Col>
            <Col span={16} style={{ textAlign: "center" }}>
              <div
                style={{
                  color: "white",
                  fontSize: 26,
                  fontWeight: "bold",
                }}
              >
                {data}
              </div>
              <h3 style={{ color: "white" }}>Departement</h3>
            </Col>
          </Row>
        </Card>
      </div>
    </Col>
  );
};

export default CBoxDepartement;
